import React from 'react';
import s from "./Form.module.scss";
import imgLogo from "../../../../../assets/img/logoModal.jpg";
import {NavLink} from "react-router-dom";
import {routes} from "../../../../../features/routes/routes";

type FormComponentPropsType = {
    closemodal: () => void
}
export const FormComponent: React.FC<FormComponentPropsType> = ({closemodal}) => {

    const logo = {
            backgroundImage: 'url(' + imgLogo + ')',
            backgroundSize: 'contain',
            backgroundPosition: 'center',
           backgroundRepeat:'no-repeat'
        }

        return(
        <form className={s.form}>
            <div className={s.logo} style={logo}> </div>
            <div className={s.closeModal} onClick={closemodal}>x</div>
            <div className={s.name}>   </div>

            <input className={s.input} name="Name" id="name" placeholder=" "/>
            <input className={s.input} name="Phone" id="email" placeholder=" "/>

            <p className={s.textareaWrapper}>   ,      
                 
                      <NavLink to={routes.privacyPolicy} onClick={closemodal}>   </NavLink></p>

            <button className={s.button} type={"submit"} name={'send message'}></button>

        </form>
    );
};

