import React from 'react';
import s from './Map.module.scss'
import map from "../../../assets/img/iconContacts/map.png";
import {NavLink} from "react-router-dom";
import {routes} from "../../routes/routes";

export const Map = () => {
    const mapImg = {
        backgroundImage: 'url(' + map + ')',
        // backgroundSize: 'contain',
        // backgroundPosition: 'center',
        // backgroundRepeat: 'no-repeat'
    }
    return (
        <div className={s.wrapContacts}>
            <div className={s.mapContainer}>
                <div className={s.mapHeader}><span></span>  </div>
                <div className={s.mapImg}>
                    <div className={s.map} style={mapImg}></div>
                </div>
            </div>
            <div className={s.getInTorchContainer}>
                <div className={s.getInTorchHeader}>   <span></span> !</div>
                <div className={s.text}>
                         ,    
                    .
                </div>
                <form className={s.form}>
                    <input className={s.input} name="Name" id="name" placeholder=" "/>
                    <input className={s.input} name="Phone" id="email" placeholder="+375(XX) XXX-XX-XX"/>
                    <div className={s.textareaWrapper}>
                        <textarea className={s.textarea} name="Message" id="message"
                                  placeholder=""/>
                    </div>
                    <p className={s.p}>   ,      
                         
                              <NavLink to={routes.privacyPolicy} className={s.span}>   </NavLink>
                    </p>
                    <div className={s.buttonCont}>
                        <button className={s.button} type={"submit"} name={'send message'}></button>
                    </div>
                </form>

            </div>
        </div>
    );
};

