import React from 'react';
import s from './AboutVeon.module.scss'
import {data} from "./data";
import {Proffesions} from "./professions/Proffesions";


export const AboutVeon = () => {
    return (
        <div className={s.container}>
            <div className={s.aboutUs}>    ,        -  -.       ,         sales@veon-tech.ru<br/>
                <br/>
                        ,      ,     !
            </div>
            <div className={s.workTime}>  ,    7-  !</div>
            <div className={s.descHeader}>     Veon-Tech?</div>
            <div className={s.desc}>
                {data.map(d=><div key={d.id} className={s.descItem}>
                    {d.desc}
                </div>)}
            </div>
            <Proffesions/>
        </div>
    );
};

